<template>
  <div class="category-container">
    <div
      class="slider"
      @touchstart="onTouchStart"
      @touchend="onTouchEnd"
    >
      <img
        v-if="currentCostume"
        :src="currentCostume.image"
        class="costume-image"
        alt="Costume"
      />

      <div class="name">
        {{ currentCostume?.name }}
      </div>
    </div>

    <div class="like-wrapper" style="position: relative; display: inline-block;">
    <button
        class="like-button"
        :disabled="isConcluded || hasVoted"
        @click="like"
    >
        ❤️ Like
    </button>

    <span
        v-if="showHeart"
        class="heart-animation"
    >❤️</span>
    </div>

    <button
      v-if="nextRoute"
      class="next-button"
      @click="$router.push(nextRoute)"
    >
      Next category →
    </button>
  </div>
</template>

<script>
import { useCompleteStore } from '@/store/store';

export default {
  name: 'CategoryBase',

  props: {
    category: {
      type: Number,
      required: true
    },
    nextRoute: {
      type: String,
      default: null
    }
  },

  data() {
    return {
      index: 0,
      touchStartX: 0,
      showHeart: false,
    };
  },

  computed: {
    store() {
      return useCompleteStore();
    },
    costumes() {
      return this.store.getCostumesByCategory(this.category);
    },
    currentCostume() {
      return this.costumes[this.index];
    },
    isConcluded() {
      return this.store.isContestConcluded;
    },
    hasVoted() {
        return this.store.hasVotedInCategory(this.category);
    }
  },

  mounted() {
    this.store.loadContestFromLocalStorage();
  },

  methods: {
    like() {
        if (!this.currentCostume) return;
        if (this.hasVoted) return;

        this.store.voteForCostume(this.category, this.currentCostume.id);
        this.store.markCategoryLiked(this.category);

        // Herz-Animation
        this.showHeart = true;
        setTimeout(() => {
            this.showHeart = false;
        }, 1000);
    },

    onTouchStart(e) {
      this.touchStartX = e.changedTouches[0].clientX;
    },

    onTouchEnd(e) {
      const deltaX = e.changedTouches[0].clientX - this.touchStartX;

      if (Math.abs(deltaX) < 50) return;

      if (deltaX < 0 && this.index < this.costumes.length - 1) {
        this.index++;
      }

      if (deltaX > 0 && this.index > 0) {
        this.index--;
      }
    }
  }
};
</script>

<style scoped>
.category-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #000;
  color: white;
}

.slider {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.costume-image {
  max-width: 100%;
  max-height: 70vh;
  object-fit: contain;
  border-radius: 12px;
}

.name {
  margin-top: 12px;
  font-size: 1.4rem;
  font-weight: 600;
}

.like-button {
  margin: 12px;
  padding: 16px;
  font-size: 1.4rem;
  border-radius: 12px;
  border: none;
  background: #ff3366;
  color: white;
}

.like-button:disabled {
  opacity: 0.5;
}

.next-button {
  margin: 12px;
  padding: 14px;
  font-size: 1.1rem;
  border-radius: 12px;
  border: none;
  background: #222;
  color: white;
}

.like-wrapper {
  position: relative;
}

.heart-animation {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%) scale(1);
  font-size: 2.4rem;
  color: #ff3366;
  animation: popHeart 1s forwards;
  pointer-events: none; /* Klicks durchlassen */
}

@keyframes popHeart {
  0% {
    opacity: 1;
    transform: translateX(-50%) scale(0.5);
  }
  50% {
    opacity: 1;
    transform: translateX(-50%) scale(1.4);
  }
  100% {
    opacity: 0;
    transform: translateX(-50%) scale(1.8);
  }
}
</style>
